<template>
  <div>
    <v-container>
      <custom-snackbar ref="customSnackbar"></custom-snackbar>
      <v-card class="mb-2">
        <v-card-title class="headline font-weight-regular chsli-color white--text">Provider Profile Maintenance</v-card-title>
        <v-layout class="row">
          <v-flex xs12 md>
            <v-card-text>
              <v-subheader class="pa-0">
                By Provider Name
                <i class="material-icons" @click="newPhysician()" style="padding-left: 8px" v-if="isAdministrator">add_box</i>
              </v-subheader>
              <v-autocomplete v-model="selectedNpi" :items="physicianLookup" persistent-hint item-text="name" item-value="npi" @change="selectPhysician(selectedNpi)" @click="reset()"></v-autocomplete>
            </v-card-text>
          </v-flex>
          <v-flex xs12 md>
            <v-card-text>
              <v-subheader class="pa-0">By Practice/Hospital Department</v-subheader>
              <v-autocomplete
                v-model="selectedId"
                :items="practices"
                persistent-hint
                item-text="group_practice"
                item-value="id"
                @change="selectPhysiciansByPractice(selectedId)"
                @click="reset()"
              ></v-autocomplete>
            </v-card-text>
          </v-flex>
        </v-layout>
      </v-card>

      <v-card v-if="showPhysicianListing">
        <v-card-title class="headline font-weight-regular chsli-color white--text">Physicians</v-card-title>
        <v-list>
          <v-list-tile v-if="physician.physician_practice_id === selectedId" v-for="(physician, index) in physicians" :key="index" avatar @click="selectPhysician(physician.npi)">
            <v-list-tile-content>
              <v-list-tile-title>{{ physician.last_name + ", " + physician.first_name }}</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
      </v-card>

      <v-card v-if="showPhysicianProfile">
        <v-card-title class="headline font-weight-regular chsli-color white--text">Profile</v-card-title>
        <v-card-text>
          <v-form ref="form">
            <v-text-field v-model="currentPhysician.action" label="Action Type" v-show="false"></v-text-field>
            <v-container fluid>
              <v-layout row wrap>
                <v-flex xs12 md6>
                  <v-text-field
                    v-model="currentPhysician.npi"
                    mask="##########"
                    v-validate="'required|length:10'"
                    data-vv-name="NPI"
                    label="NPI"
                    required
                    :error-messages="errors.collect('NPI')"
                    :disabled="!isAdministrator"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md6>
                  <v-text-field v-model="currentPhysician.specialty" label="Specialty" :disabled="!isAdministrator"></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md2>
                  <v-text-field
                    v-model="currentPhysician.title"
                    label="Title"
                    data-vv-name="Title"
                    v-validate="'required'"
                    required
                    :error-messages="errors.collect('Title')"
                    :disabled="!isAdministrator"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md3>
                  <v-text-field
                    v-model="currentPhysician.last_name"
                    data-vv-name="Last Name"
                    label="Last Name"
                    v-validate="'required'"
                    required
                    :error-messages="errors.collect('Last Name')"
                    :disabled="!isAdministrator"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md3>
                  <v-text-field
                    v-model="currentPhysician.first_name"
                    data-vv-name="First Name"
                    label="First Name"
                    v-validate="'required'"
                    required
                    :error-messages="errors.collect('First Name')"
                    :disabled="!isAdministrator"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 md4 style="display: flex">
                  <v-text-field
                    v-model="currentPhysician.email"
                    data-vv-name="Email"
                    v-validate="'email'"
                    label="Email"
                    :error-messages="errors.collect('Email')"
                    :disabled="!isAdministrator"
                  ></v-text-field>
                  <a v-if="currentPhysician.email" :href="`mailto:${currentPhysician.email}`" style="margin: auto">
                    <i class="material-icons">email</i>
                  </a>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs4>
                  <v-select v-model="currentPhysician.billing" :items="billingTypes" item-text="value" item-value="id" label="Billing" :disabled="!isAdministrator"></v-select>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md4>
                  <v-select v-model="currentPhysician.emr_id" :items="emrTypes" item-text="value" item-value="id" label="EMR" :disabled="!isAdministrator"></v-select>
                </v-flex>
                <v-flex xs12 md4>
                  <v-select v-model="currentPhysician.billing_system_id" :items="billingSystems" item-text="value" item-value="id" label="Billing system" :disabled="!isAdministrator"></v-select>
                </v-flex>
                <v-flex xs12 md4>
                  <v-text-field v-model="currentPhysician.directory_for_ads" label="Directory for ADS" :disabled="!isAdministrator"></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12>
                  <v-select
                    multiple
                    chips
                    v-model="currentPhysician.physician_entity_id"
                    :items="entities"
                    item-text="entity_name"
                    item-value="id"
                    label="Entity"
                    :disabled="!isAdministrator"
                  ></v-select>
                  <PhysicianEntity v-for="ent in currentPhysician.physician_entity_id" v-bind:key="ent.entity_id" :entity="getEntity(ent)"></PhysicianEntity>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12>
                  <v-select
                    v-model="currentPhysician.physician_practice_id"
                    @change="getPractice()"
                    :items="practices"
                    item-text="group_practice"
                    item-value="id"
                    label="Practice/Hospital Department"
                    :disabled="!isAdministrator"
                  ></v-select>
                  <PhysicianPractice :practice="getPractice()"></PhysicianPractice>
                </v-flex>
              </v-layout>
              <p>Primary Address</p>
              <v-layout row wrap>
                <v-flex xs12>
                  <v-text-field v-model="currentPhysician.practice_location1_address1" label="Address1" :disabled="!isAdministrator"></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12>
                  <v-text-field v-model="currentPhysician.practice_location1_address2" label="Address2" :disabled="!isAdministrator"></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md4>
                  <v-text-field v-model="currentPhysician.practice_location1_city" label="City" :disabled="!isAdministrator"></v-text-field>
                </v-flex>
                <v-flex xs12 md4>
                  <v-text-field v-model="currentPhysician.practice_location1_state" label="State" :disabled="!isAdministrator"></v-text-field>
                </v-flex>
                <v-flex xs12 md4>
                  <v-text-field v-model="currentPhysician.practice_location1_zip" label="Zip" :disabled="!isAdministrator"></v-text-field>
                </v-flex>
              </v-layout>
              <p>Additional Address</p>
              <v-layout row wrap>
                <v-flex xs12>
                  <v-text-field v-model="currentPhysician.practice_location2_address1" label="Address1" :disabled="!isAdministrator"></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12>
                  <v-text-field v-model="currentPhysician.practice_location2_address2" label="Address2" :disabled="!isAdministrator"></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md4>
                  <v-text-field v-model="currentPhysician.practice_location2_city" label="City" :disabled="!isAdministrator"></v-text-field>
                </v-flex>
                <v-flex xs12 md4>
                  <v-text-field v-model="currentPhysician.practice_location2_state" label="State" :disabled="!isAdministrator"></v-text-field>
                </v-flex>
                <v-flex xs12 md4>
                  <v-text-field v-model="currentPhysician.practice_location2_zip" label="Zip" :disabled="!isAdministrator"></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12>
                  <v-textarea v-model="currentPhysician.comments" box label="Comments" auto-grow></v-textarea>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs4>
                  <v-menu
                    ref="menuDatePickerNextAudit"
                    :close-on-content-click="false"
                    v-model="menuDatePickerNextAudit"
                    :nudge-right="40"
                    :return-value.sync="currentPhysician.next_scheduled_audit_date"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                  >
                    <v-text-field slot="activator" v-model="currentPhysician.next_scheduled_audit_date" label="Next Audit Date" prepend-icon="event" readonly></v-text-field>
                    <v-date-picker
                      v-model="currentPhysician.next_scheduled_audit_date"
                      @input="$refs.menuDatePickerNextAudit.save(currentPhysician.next_scheduled_audit_date)"
                      @change="currentPhysician.next_scheduled_audit_date_dirty = true"
                      :disabled="!isAdministrator"
                      xxtype="month"
                    ></v-date-picker>
                  </v-menu>
                </v-flex>
                <v-flex xs4>
                  <v-menu
                    ref="menuDatePickerStart"
                    :close-on-content-click="false"
                    v-model="menuDatePickerStart"
                    :nudge-right="40"
                    :return-value.sync="currentPhysician.start_date"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="currentPhysician.start_date"
                      label="Start Date"
                      prepend-icon="event"
                      readonly
                      v-validate="{
                        rules: { required: this.isTermDateRequired },
                      }"
                      data-vv-name="Start Date"
                      :error-messages="errors.collect('Start Date')"
                    ></v-text-field>
                    <v-date-picker v-model="currentPhysician.start_date" @input="$refs.menuDatePickerStart.save(currentPhysician.start_date)"></v-date-picker>
                  </v-menu>
                </v-flex>
                <v-flex xs4>
                  <v-checkbox v-model="currentPhysician.terminated" label="Terminated" :disabled="!isAdministrator"></v-checkbox>
                </v-flex>
                <v-flex xs4>
                  <v-menu
                    v-if="this.currentPhysician.terminated"
                    ref="menuDatePicker"
                    :close-on-content-click="false"
                    v-model="menuDatePicker"
                    :nudge-right="40"
                    :return-value.sync="currentPhysician.terminated_date"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="currentPhysician.terminated_date"
                      label="Terminated Date"
                      prepend-icon="event"
                      readonly
                      v-validate="{
                        rules: { required: this.isTermDateRequired },
                      }"
                      data-vv-name="Terminated Date"
                      :error-messages="errors.collect('Terminated Date')"
                    ></v-text-field>
                    <v-date-picker v-model="currentPhysician.terminated_date" @input="$refs.menuDatePicker.save(currentPhysician.terminated_date)"></v-date-picker>
                  </v-menu>
                </v-flex>
              </v-layout>
            </v-container>
            <v-btn @click="savePhysician()">Save</v-btn>
            <v-btn @click="cancelPhysician()">Cancel</v-btn>
          </v-form>
        </v-card-text>
      </v-card>
    </v-container>
    <PhysicianAudits v-if="showPhysicianProfile" :npi="selectedNpi"></PhysicianAudits>
    <EducationListing v-if="showPhysicianProfile" :npi="selectedNpi" :startDate="currentPhysician.start_date"></EducationListing>
    <confirmation-dialog ref="confirmationDialog" title="Confirm" message="Are you sure you want to cancel your changes?"></confirmation-dialog>
  </div>
</template>

<script>
import ConfirmationDialog from "@/components/ConfirmationDialog";
import CustomSnackbar from "@/components/Snackbar";
import PhysicianEntity from "@/components/PhysicianAdministration/Entity";
import PhysicianPractice from "@/components/PhysicianAdministration/Practice";
import PhysicianAudits from "@/components/AuditListing";
import EducationListing from "@/components/EducationListing";

const initializePhysician = () => {
  return {
    action: "",
    id: "",
    npi: "",
    last_name: "",
    first_name: "",
    title: "",
    specialty: "",
    billing: "",
    practice_location1_address1: "",
    practice_location1_address2: "",
    practice_location1_city: "",
    practice_location1_state: "",
    practice_location1_zip: "",
    practice_location2_address1: "",
    practice_location2_address2: "",
    practice_location2_city: "",
    practice_location2_state: "",
    practice_location2_zip: "",
    physician_entity_id: [],
    physician_practice_id: null,
    comments: "",
    terminated: 0,
    terminated_date: "",
    start_date: "",
    emr_id: -1,
    billing_system_id: -1,
    directory_for_ads: "",
    email: "",
  };
};

export default {
  name: "Main",
  components: {
    ConfirmationDialog,
    CustomSnackbar,
    PhysicianEntity,
    PhysicianPractice,
    PhysicianAudits,
    EducationListing,
  },
  data: () => ({
    menuDatePicker: null,
    menuDatePickerStart: null,
    menuDatePickerNextAudit: null,
    selectedNpi: null,
    selectedId: null,
    showPhysicianProfile: false,
    showPhysicianListing: false,
    currentPhysician: initializePhysician(),
  }),
  mounted() {
    //   this.currentPhysician = this.changer("");
  },
  computed: {
    isAdministrator() {
      return this.$store.getters.CURRENT_USER.role === "administrator";
      //      return true;
    },
    physicians() {
      return this.$store.getters.ALL_PHYSICIAN;
    },
    practices() {
      return this.$store.getters.PRACTICE;
    },
    physicianLookup() {
      return this.physicians.map((row) => {
        let res = {};
        res.name = row.last_name + ", " + row.first_name;
        res.npi = row.npi;
        return res;
      });
    },
    entities() {
      return this.$store.getters.ENTITY;
    },
    isTermDateRequired() {
      return this.currentPhysician.terminated;
    },
    emrTypes() {
      return this.$store.getters.APPLICATION_LISTS.filter((row) => row.keyword === "EMR").map((row) => row);
    },
    billingSystems() {
      return this.$store.getters.APPLICATION_LISTS.filter((row) => row.keyword === "BILLING_SYSTEM").map((row) => row);
    },
    billingTypes() {
      return this.$store.getters.APPLICATION_LISTS.filter((row) => row.keyword === "PROVIDER_BILLING_TYPE").map((row) => row);
    },
  },
  methods: {
    newPhysician: function() {
      if (this.isAdministrator) {
        this.currentPhysician = initializePhysician(); // re-initialize fields
        this.currentPhysician.action = "new"; // adding a new physician
        this.showPhysicianProfile = true;
      }
    },
    selectPhysician: function(sel) {
      // this.currentPhysician = this.physicians.find(p => p.npi === sel);
      this.currentPhysician = Object.assign({}, this.physicians.find((p) => p.npi === sel));

      if (!Array.isArray(this.currentPhysician.physician_entity_id)) {
        // convert string of numbers to array of integers.  TODO - is there a better way?
        this.currentPhysician.physician_entity_id = this.currentPhysician.physician_entity_id.split(",").map((el) => {
          let n = Number(el);
          return n === 0 ? n : n || el;
        });
      }
      this.currentPhysician.action = "update"; // updating an existing physician
      this.showPhysicianProfile = true;
      this.showPhysicianListing = false;
    },
    selectPhysiciansByPractice: function(selectedId) {
      this.showPhysicianListing = true;
      this.showPhysicianProfile = false;
    },
    savePhysician: function() {
      this.$validator.validateAll().then((result) => {
        if (!result) {
          return false;
        } else {
          let dispatchType = "";
          if (this.currentPhysician.action === "new") {
            dispatchType = "NEW_PHYSICIAN";
          } else {
            dispatchType = "UPDATE_PHYSICIAN";
          }
          this.currentPhysician.user_role = this.$store.getters.CURRENT_USER.role; // check role on backend

          this.$store
            .dispatch(dispatchType, this.currentPhysician)
            .then((data) => {
              if (data === true) {
                this.showMessage("success", "Record sucessfully saved");
                if (this.currentPhysician.action === "new") {
                  this.currentPhysician = initializePhysician(); // re-initialize fields
                }
              } else {
                this.showMessage("error", "There was a problem saving your changes.  MSG - " + data);
              }
            })
            .catch((err) => {
              console.log("err - ", err);
              this.showMessage("error", "There was a problem saving your changes.  MSG - " + err.originalError.info.message);
            });
        }
      });
    },
    cancelPhysician: function() {
      this.$refs.confirmationDialog.confirmation().then((res) => {
        if (res) {
          this.showPhysicianProfile = false;
          this.selectedNpi = null;
          this.currentPhysician = initializePhysician;
          if (this.selectedId) {
            this.showPhysicianListing = true;
          }
        }
      });
    },
    getEntity: function(id) {
      return this.entities.find((e) => e.id == id);
    },
    getPractice: function() {
      return this.practices.find((e) => e.id == this.currentPhysician.physician_practice_id);
    },
    setTerminated: function() {
      if (this.currentPhysician.terminated) {
        this.currentPhysician.terminated_date = new Date(); // set date
      } else {
        this.currentPhysician.terminated_date = "";
      }
    },
    showMessage: function(type, message) {
      this.$refs.customSnackbar.showSnackbar(message, type, 3000, false);
    },
    reset: function() {
      this.showPhysicianListing = false;
      this.showPhysicianProfile = false;
      this.selectedNpi = null;
      this.selectedId = null;
    },
  },
};
</script>

<style>
i:hover {
  cursor: pointer;
}
</style>
