<template>
  <div>
    <v-layout>
      <v-flex>
        <h3>Current Audits</h3>
      </v-flex>
    </v-layout>
    <v-layout>
      <v-flex>
        <v-select v-model="inAuditMonth" item-value="indx" item-text="month" label="Current Audit Month" :items="months" return-object></v-select>
      </v-flex>
      <v-flex>
        <v-text-field v-model="inAuditYear" label="Current Audit Year"></v-text-field>
      </v-flex>
      <v-flex>
        <v-btn @click="runReport()">Run</v-btn>
      </v-flex>
    </v-layout>
    <div v-if="!initialSearch">
      <v-layout>
        <v-flex xs2 cols="2">
          <img width="125" src="../assets/logo.png" />
        </v-flex>
        <v-flex>
          <div style="font-size:2em;font-weight:bold;margin:auto;text-align:center;padding-top:10px;">{{ inAuditMonth.month }} {{ inAuditYear }} {{ reportTitle }}</div>
        </v-flex>
      </v-layout>
      <v-layout dense>
        <v-flex>
          <span xs2 class="heading">Entity</span>
        </v-flex>
        <v-flex xs2 cols="2">
          <span class="heading">Practice/Hospital Department</span>
        </v-flex>
        <v-flex xs2>
          <span class="heading">Group Name</span>
        </v-flex>
        <v-flex xs2>
          <span class="heading">Provider Name</span>
        </v-flex>
        <v-flex xs2>
          <span class="heading">NPI</span>
        </v-flex>
        <v-flex xs2>
          <span class="heading">Auditor</span>
        </v-flex>
        <v-flex xs2>
          <span class="heading">CMO</span>
        </v-flex>
        <v-flex xs2>
          <span class="heading">Director</span>
        </v-flex>
        <v-flex xs2>
          <span class="heading">Contact Person</span>
        </v-flex>
        <v-flex xs2>
          <span class="heading">Audit Type</span>
        </v-flex>
        <v-flex xs2>
          <span class="heading">Completed Date</span>
        </v-flex>
      </v-layout>
      <div v-for="(row, indx) in reportData" :key="indx">
        <v-layout dense style="font-size:12px;">
          <v-flex xs2>{{ row.entity_name }}</v-flex>
          <v-flex xs2 cols="2">{{ row.group_practice }}</v-flex>
          <v-flex xs2>{{ row.group_name }}</v-flex>
          <v-flex xs2>{{ row.provider_name }}</v-flex>
          <v-flex xs2>{{ row.npi }}</v-flex>
          <v-flex xs2>{{ row.auditor }}</v-flex>
          <v-flex xs2>{{ row.chief_medical_officer }}</v-flex>
          <v-flex xs2>{{ row.practice_director }}</v-flex>
          <v-flex xs2>{{ row.contact1_name }}</v-flex>
          <v-flex xs2>{{ row.audit_type }}</v-flex>
          <v-flex xs2>{{ row.audit_completed_date }}</v-flex>
        </v-layout>
      </div>
      <v-layout>
        <v-flex>
          <v-btn @click="csvExport()">Download File</v-btn>
        </v-flex>
      </v-layout>
    </div>
  </div>
</template>

<script>
import httpService from "../httpService";
const { parse } = require("json2csv");

import Config from "../config";

export default {
  data: () => ({
    reportTitle: "Current Audits",
    reportData: [],
    inAuditMonth: { indx: "08", month: "August" },
    inAuditYear: "2020",
    inAuditDate: "",
    initialSearch: true,
    sampledata: "",
    months: [
      {
        indx: "01",
        month: "January",
      },
      {
        indx: "02",
        month: "February",
      },
      {
        indx: "03",
        month: "March",
      },
      {
        indx: "04",
        month: "April",
      },
      {
        indx: "05",
        month: "May",
      },
      {
        indx: "06",
        month: "June",
      },
      {
        indx: "07",
        month: "July",
      },
      {
        indx: "08",
        month: "August",
      },
      {
        indx: "09",
        month: "September",
      },
      {
        indx: "10",
        month: "October",
      },
      {
        indx: "11",
        month: "November",
      },
      {
        indx: "12",
        month: "December",
      },
    ],
  }),
  computed: {},
  methods: {
    runReport() {
      httpService.get(`${Config.apiRoot}/reports/current-audits/${this.inAuditMonth.indx}/${this.inAuditYear}`, (status, data) => {
        console.log("status - ", status);
        this.reportData = data;
        this.initialSearch = false;
      });
    },
    csvExport() {
      const fields = [
        "entity_name",
        "group_practice",
        "group_name",
        "provider_name",
        "npi",
        "auditor",
        "chief_medical_officer",
        "practice_director",
        "contact1_name",
        "audit_type",
        "audit_completed_date",
      ];
      const opts = { fields };

      try {
        const csvHeader = `"${this.inAuditMonth.month} ${this.inAuditYear} Current Audits"\n\n`;
        const csvData = parse(this.reportData, opts);

        // const arrData = this.reportData;
        let csvContent = "data:text/csv;charset=utf-8," + csvHeader + csvData;
        // csvContent += [Object.keys(arrData[0]).join(";"), ...arrData.map((item) => Object.values(item).join(";"))].join("\n").replace(/(^\[)|(\]$)/gm, "");

        const data = encodeURI(csvContent);

        this.sampledata = csvContent;
        const link = document.createElement("a");
        link.setAttribute("href", data);
        link.setAttribute("download", "export.csv");
        link.click();
      } catch (err) {
        console.error(err);
      }
    },
  },
};
</script>

