<template>
  <div>
    <v-layout>
      <v-flex>
        <h3>Missing New Provider Educations</h3>
      </v-flex>
    </v-layout>
    <div v-if="!initialSearch">
      <v-layout>
        <v-flex xs2 cols="2">
          <img width="125" src="../assets/logo.png" />
        </v-flex>
        <v-flex>
          <div style="font-size:2em;font-weight:bold;margin:auto;text-align:center;padding-top:10px;">{{ reportTitle }}</div>
        </v-flex>
      </v-layout>
      <v-layout dense>
        <v-flex xs1>
          <span class="heading">Entity</span>
        </v-flex>
        <v-flex xs2>
          <span class="heading">Practice/Hospital Department</span>
        </v-flex>
        <v-flex xs2>
          <span class="heading">Group Name</span>
        </v-flex>
        <v-flex xs2>
          <span class="heading">Provider Name</span>
        </v-flex>
        <v-flex xs2>
          <span class="heading">NPI</span>
        </v-flex>
        <v-flex xs2>
          <span class="heading">Auditor</span>
        </v-flex>
        <v-flex xs2>
          <span class="heading">CMO</span>
        </v-flex>
        <v-flex xs2>
          <span class="heading">Director</span>
        </v-flex>
        <v-flex xs2>
          <span class="heading">Contact Person</span>
        </v-flex>
      </v-layout>
      <div v-for="(row, indx) in reportData" :key="indx">
        <v-layout dense>
          <v-flex xs1>{{ row.entity_name }}</v-flex>
          <v-flex xs2>{{ row.group_practice }}</v-flex>
          <v-flex xs2>{{ row.group_name }}</v-flex>
          <v-flex xs2>{{ row.provider_name }}</v-flex>
          <v-flex xs2>{{ row.npi }}</v-flex>
          <v-flex xs2>{{ row.auditor }}</v-flex>
          <v-flex xs2>{{ row.chief_medical_officer }}</v-flex>
          <v-flex xs2>{{ row.practice_director }}</v-flex>
          <v-flex xs2>{{ row.contact1_name }}</v-flex>
        </v-layout>
      </div>
      <v-layout>
        <v-flex>
          <v-btn @click="csvExport()">Download File</v-btn>
        </v-flex>
      </v-layout>
    </div>
  </div>
</template>

<script>
import httpService from "../httpService";
const { parse } = require("json2csv");

import Config from "../config";

export default {
  data: () => ({
    reportTitle: "Missing New Provider Educations",
    reportData: [],
    initialSearch: true,
  }),
  computed: {},
  mounted: function() {
    httpService.get(`${Config.apiRoot}/reports/missing-new-educations`, (status, data) => {
      console.log("status - ", status);
      this.reportData = data;
      this.initialSearch = false;
    });
  },
  methods: {
    csvExport() {
      console.log("Starting Export...");
      const fields = ["entity_name", "group_practice", "group_name", "provider_name", "npi", "auditor", "chief_medical_officer", "practice_director", "contact1_name"];
      const opts = { fields };

      try {
        const csvHeader = `"${this.reportTitle}"\n\n`;
        const csvData = parse(this.reportData, opts);

        console.log("csvHeader - ", csvHeader);
        console.log("csvData - ", csvData);

        let csvContent = "data:text/csv;charset=utf-8," + csvHeader + csvData;

        const data = encodeURI(csvContent);

        this.sampledata = csvContent;
        const link = document.createElement("a");
        link.setAttribute("href", data);
        link.setAttribute("download", "missing-new-educations.csv");
        link.click();
      } catch (err) {
        console.error("error - ", err);
      }
    },
  },
};
</script>

