<template>
  <div>
    <v-container>
      <v-layout row wrap v-if="isAdministrator">
        <v-flex xs12 md4 offset-md8 class="pr-1" right>
          <v-select v-model="auditorId" :items="auditors" item-text="fullname" item-value="id" box label="Auditors"></v-select>
        </v-flex>
      </v-layout>

      <v-layout row wrap>
        <v-flex xs12 md12 class="pr-1">
          <v-card class="mb-2">
            <v-card-title class="headline font-weight-regular chsli-color white--text">Practice/Hospital Departments</v-card-title>
            <v-card-text>
              <v-data-table dense :key="key" :headers="tableHeadersPractice" :items="practiceAudits" class="evelation-1" :pagination.sync="pagination" group-by="group_practice" show-group-by="true">
                <template slot="items" slot-scope="{ item }">
                  <tr @clickaa="auditPractice(item.practice_audit_id)">
                    <td>{{ item.entity_name }} - {{ item.group_practice }}</td>
                    <td>{{ item.audit_date }}</td>
                    <td>
                      {{ item.in_progress == 0 ? "Complete" : "Pending" }}
                    </td>
                    <td>{{ item.in_progress + item.complete }}</td>
                    <td>{{ item.in_progress }}</td>
                    <td>{{ item.complete }}</td>
                    <td>
                      {{ computePracticeScore_1(item.score, item.provider_count) }}
                    </td>
                    <td>
                      <v-tooltip bottom>
                        <i class="material-icons" @click="auditPractice(item.practice_audit_id)" slot="activator">edit</i>
                        <span>View/Edit Encounter</span>
                      </v-tooltip>
                      <v-tooltip bottom v-if="isAdministrator">
                        <i class="material-icons" @click="deletePracticeAudit(item.practice_audit_id, $event)" slot="activator">delete</i>
                        <span>Delete Practice Audit</span>
                      </v-tooltip>
                    </td>
                  </tr>
                </template>
              </v-data-table>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
    <confirmation-dialog ref="confirmationDialog" :title="confirmation.title" :message="confirmation.message"></confirmation-dialog>
  </div>
</template>

<script>
import Config from "../config";
import Router from "../router";
import httpService from "../httpService";
import ConfirmationDialog from "@/components/ConfirmationDialog";
import CustomSnackbar from "@/components/Snackbar";

export default {
  name: "Main",
  components: { ConfirmationDialog, CustomSnackbar },
  data: () => ({
    audits: [],
    practiceAudits: [],
    auditorId: null,
    key: 0,
    tableHeadersPractice: [
      { text: "Practice Group", value: "practice" },
      { text: "Audit Date", value: "audit_date" },
      { text: "Status", value: "status" },
      { text: "Total Providers", value: "total_providers" },
      { text: "In Progress", value: "in_progress" },
      { text: "Completed", value: "complete" },
      { text: "Score", value: "total_audit_score" },
      { text: "Actions", value: "adminDelete" },
    ],
    pagination: {
      rowsPerPage: 10,
    },
    confirmation: {
      title: "",
      message: "",
    },
  }),
  async mounted() {
    this.auditorId = this.currentUser.id; // when are computed items ready?
    this.getAudits();
  },
  watch: {
    auditorId: function(id) {
      this.getAudits();
    },
  },
  computed: {
    auditors() {
      return this.$store.getters.ALL_USERS;
    },
    currentUser() {
      return this.$store.getters.CURRENT_USER;
    },
    isAdministrator() {
      return this.currentUser.role === "administrator";
    },
  },
  methods: {
    getAudits() {
      httpService.get(`${Config.apiRoot}/audit/dashboard2/${this.auditorId}`, (status, data) => {
        this.audits = data.message.filter(
          // filter out the practice audits w/o provider's assigned yet
          (obj) => obj.provider_audit_id !== null
        );
        this.practiceAudits = this.groupBy(data.message, "provider_audit_id");

        //preset 2 fields for sorting
        for (let i = 0; i < this.audits.length; i++) {
          this.audits[i].status = this.getStatus(this.audits[i]);
          this.audits[i].in_progress = this.calcIncomplete(this.audits[i]);

          // console.log("this.audits[i].provider_name - ", this.audits[i].provider_name);
          // console.log("this.audits[i].in_progress - ", this.audits[i].in_progress);
        }
        this.key++;
      });
    },
    calcIncomplete(provider) {
      // incomplete returns 1 if there are no joined rows, check then adjust
      if (provider.count_of_linked_rows === 0) {
        return 0;
      }
      return provider.incomplete;
    },
    getStatus(provider) {
      if (provider.audit_completed_date) {
        return "Completed";
      }
      return "In Progress";
    },
    groupBy: function(source, key) {
      // building custom json from the rows for 2nd data table
      let tmp = [];
      for (let i = 0; i < source.length; i++) {
        const indx = tmp.findIndex((obj) => obj.practice_audit_id == source[i].practice_audit_id);

        // need to set the score
        // tmp[i].score = 0;

        var tmpScore = 0;
        let tmpAddToAcount = 0;
        if (!isNaN(parseInt(source[i].current_audit_score))) {
          // if score is empty, don't include it in score or average
          tmpScore = parseInt(source[i].current_audit_score);
          tmpAddToAcount = 1;
        }

        if (indx == -1) {
          // id doesn't exist, so push
          let tmpComplete = 0;
          let tmpPending = 0;
          if (source[i].audit_completed_date !== null || (source[i].complete != 0 && source[i].complete == source[i].count_of_linked_rows)) {
            tmpComplete++;
          } else {
            tmpPending++;
          }

          tmp.push({
            practice_audit_id: source[i].practice_audit_id,
            entity_name: source[i].entity_name,
            group_practice: source[i].group_practice,
            audit_date: source[i].audit_date,
            in_progress: tmpPending,
            complete: tmpComplete,
            score: tmpScore,
            count_of_linked_rows: source[i].count_of_linked_rows,
            provider_count: tmpAddToAcount,
          });
        } else {
          // index found, so just add to totals
          if (source[i].audit_completed_date !== null || (source[i].complete != 0 && source[i].complete == source[i].count_of_linked_rows)) {
            tmp[indx].complete++;
          } else {
            tmp[indx].in_progress++;
          }

          tmp[indx].count_of_linked_rows += source[i].count_of_linked_rows;
          tmp[indx].score += tmpScore;
          tmp[indx].provider_count += tmpAddToAcount;
        }
      }
      return tmp;
    },
    deletePracticeAudit: function(id, event) {
      event.stopPropagation();

      this.confirmation.title = "Delete Practice Audit?";
      this.confirmation.message = "Are you sure you want to delete this practice audit?";
      this.$refs.confirmationDialog.confirmation().then((res) => {
        if (res) {
          httpService.delete(`${Config.apiRoot}/audit/practice/${id}`, (status, data) => {
            if (data.success) {
              this.showMessage("success", "Practice Audit sucessfully deleted");
              this.popEncounter(index);
            } else {
              this.showMessage("error", "There was a problem deleting the practice.  MSG - " + data.message.originalError.info.message);
            }
          });
        }
      });
    },
    auditPractice: function(id) {
      Router.push({
        name: "audit-practice",
        params: { practice_audit_id: id },
      });
    },
    showMessage: function(type, message) {
      this.$refs.customSnackbar.showSnackbar(message, type, 3000, true);
    },
  },
};
</script>

<style>
.v-datatable th,
.v-datatable td {
  padding-left: 5px !important;
  padding-right: 5px !important;
}
/* .border {
  border: solid;
} */
i {
  padding: 0 5px;
}
i:hover {
  cursor: pointer;
}
.hover {
  cursor: pointer;
  text-decoration: underline;
}
.small-font {
  font-size: 15px;
}
.chsli-color {
  background-color: #320484 !important;
}
</style>
