<template>
  <div class="home">
    <v-container>
      <v-layout mt-3>
        <v-flex cols="3">
          <v-select v-model="report" item-text="name" item-value="name" :items="reportListView" label="Report"></v-select>
        </v-flex>
      </v-layout>
      <Score-Card v-if="report === 'Audit Scorecard'"></Score-Card>
      <Executive-Summary v-if="report === 'Executive Summary'"></Executive-Summary>
      <Projected-Audits v-if="report === 'Projected Audits'"></Projected-Audits>
      <Current-Audits v-if="report === 'Current Audits'"></Current-Audits>
      <Audit-History v-if="report === 'Audit History'"></Audit-History>
      <Missing-New-Educations v-if="report === 'Missing New Provider Educations'"></Missing-New-Educations>
    </v-container>
  </div>
</template>

<script>
// @ is an alias to /src
import ScoreCard from "@/components/rptScorecard.vue";
import ExecutiveSummary from "@/components/rptExecutiveSummary.vue";
import ProjectedAudits from "@/components/rptProjectedAudits.vue";
import CurrentAudits from "@/components/rptCurrentAudits.vue";
import AuditHistory from "@/components/rptAuditHistory.vue";
import MissingNewEducations from "@/components/rptMissingNewEducations.vue";

// import NewProviderEducation from "@/components/NewProviderEducation.vue";
// import AuditProductivity from "@/components/AuditProductivity.vue";

export default {
  name: "Home",
  components: {
    ScoreCard,
    ExecutiveSummary,
    ProjectedAudits,
    CurrentAudits,
    AuditHistory,
    MissingNewEducations,
  },
  data: () => ({
    report: "Audit Scorecard",
    reportListBad: ["Audit Scorecard", "Executive Summary", "Projected Audits", "Current Audits", "Audit History"],
    reportList: [
      { name: "Audit History", role: ["user", "administrator"] },
      { name: "Audit Scorecard", role: ["user", "administrator"] },
      { name: "Current Audits", role: ["administrator"] },
      { name: "Executive Summary", role: ["user", "administrator"] },
      { name: "Missing New Provider Educations", role: ["administrator"] },
      { name: "Projected Audits", role: ["administrator"] },
    ],
  }),
  computed: {
    currentUserRole() {
      return this.$store.getters.CURRENT_USER.role;
    },
    reportListView() {
      return this.reportList.filter((el) => el.role.indexOf(this.currentUserRole) >= 0);
    },
  },
};
</script>
