<template>
  <div>
    <v-layout>
      <v-flex>
        <h3>{{ reportTitle }}</h3>
      </v-flex>
    </v-layout>
    <v-layout>
      <v-layout row wrap>
        <v-flex xs12 md4 pr-1>
          <v-select v-model="inEntity" :items="entities" item-text="entity_name" item-value="id" label="Entity"></v-select>
        </v-flex>
        <v-flex xs12 md8>
          <v-autocomplete v-model="inPractice" :items="filteredPractices" persistent-hint item-text="group_practice" item-value="id" label="Practice"></v-autocomplete>
        </v-flex>
      </v-layout>
      <v-flex>
        <v-text-field v-model="inAuditDt" label="Audit Date (MMYYYY)"></v-text-field>
      </v-flex>
      <v-flex>
        <v-btn @click="generateReport()">Run</v-btn>
      </v-flex>
    </v-layout>
    <div id="toPrint" style="width:95%">
      <v-container>
        <div v-for="aType in distinctAuditTypes" :key="aType">
          <v-layout>
            <v-flex xs2 cols="2">
              <img width="125" src="../assets/logo.png" />
            </v-flex>
            <v-flex>
              <div
                style="
                font-size: 2em;
                font-weight: bold;
                margin-left: 160px;                
                padding-top: 15px;
              "
              >
                {{ reportTitle }}
              </div>
            </v-flex>
          </v-layout>
          <v-divider></v-divider>
          <div class="provider">
            <v-layout dense mb-1>
              <v-flex xs9 cols="9">
                <div>
                  <span class="heading">Practice Name:</span>
                  {{ reportData[0].group_practice }}
                </div>
              </v-flex>
              <v-flex xs3 cols="3">
                <div>
                  <span class="heading">Risk Score:</span>
                  {{ computePracticeScore(aType) }}
                </div>
              </v-flex>
            </v-layout>
            <v-layout dense mb-1>
              <v-flex xs9 cols="9">
                <div>
                  <span class="heading">Entity:</span>
                  {{ reportData[0].entity_name }}
                </div>
              </v-flex>
              <v-flex>
                <div>
                  <span class="heading">Risk Level:</span>
                  {{ computePracticeAuditLevel(aType) }}
                </div>
              </v-flex>
            </v-layout>
            <v-layout dense>
              <v-flex xs9 cols="9">
                <div>
                  <span class="heading">Number of Records Reviewed:</span>
                  {{ computeTotalRows(aType) }}
                </div>
              </v-flex>
            </v-layout>
          </div>
          <v-layout dense class="center-title">
            <v-flex xs3 cols="3">
              <span class="heading">Provider Name</span>
            </v-flex>
            <v-flex xs2>
              <span class="heading">NPI</span>
            </v-flex>
            <v-flex xs3>
              <span class="heading">Audit Completed Date</span>
            </v-flex>
            <v-flex xs2>
              <span class="heading center-title">Audit Type</span>
            </v-flex>
            <v-flex xs1>
              <span class="heading center-title"># of Records</span>
            </v-flex>
            <v-flex xs1>
              <span class="heading center-title">Score</span>
            </v-flex>
            <v-flex xs2>
              <span class="heading center-title">Risk Level</span>
            </v-flex>
            <v-flex xs3 cols="3">
              <span class="heading">Issues Identified</span>
            </v-flex>
          </v-layout>
          <div v-for="(row, indx) in reportData" :key="indx">
            <v-layout mb-1 v-if="row.audit_type == aType">
              <v-flex xs3 cols="3">{{ row.first_name }} {{ row.last_name }}, {{ row.title }}</v-flex>
              <v-flex xs2>{{ row.npi }}</v-flex>
              <v-flex xs3 class="left">{{ row.completed_date }}</v-flex>
              <v-flex xs2 class="left">{{ row.audit_type }}</v-flex>
              <v-flex xs1 class="left">{{ row.row_count }}</v-flex>
              <v-flex xs1 class="left">{{ computeProviderScore(row.audit_score, row.row_count) }}</v-flex>
              <v-flex xs2 class="left">{{ computeRisk(computeProviderScore(row.audit_score, row.row_count)) }}</v-flex>
              <v-flex xs3 cols="3">{{ formatFailedReason(row.fail_reason) }}</v-flex>
            </v-layout>
          </div>
          <div class="html2pdf__page-break"></div>
        </div>
      </v-container>
    </div>
  </div>
</template>

<script>
import httpService from "../httpService";
import html2pdf from "html2pdf.js";

import Config from "../config";

export default {
  data: () => ({
    reportTitle: "Executive Summary Report",
    reportData: [
      {
        npi: "",
        last_name: "",
        first_name: "",
        title: "",
        physician_entity_id: "",
        id: 0,
        entity_name: "",
        group_practice: "",
        audit_date: "",
        completed_date: null,
        audit_type: null,
        physician_audit_id: 0,
        audit_score: 0,
        row_count: 0,
        fail_reason: [
          {
            failed_reason: "",
          },
        ],
      },
    ],
    inEntity: "",
    inPractice: "",
    inAuditDt: "",
  }),
  mounted() {},
  computed: {
    entities() {
      return this.$store.getters.ENTITY;
    },
    practices() {
      return this.$store.getters.PRACTICE;
    },
    filteredPractices() {
      return this.practices.filter((obj) => obj.entity_id === this.inEntity);
    },
    distinctAuditTypes() {
      return [...new Set(this.reportData.map((el) => el.audit_type))];
    },
  },
  methods: {
    sleep(ms) {
      return new Promise((resolve) => setTimeout(resolve, ms));
    },
    formatFailedReason(reasons) {
      const arr = [...new Set(reasons.map((item) => item.failed_reason.split(",")))].flat();
      return arr
        .filter((value, index, self) => {
          return self.indexOf(value) === index;
        })
        .toString()
        .replace(/,/g, ", ");
    },
    generateReport() {
      // api built to receive names.  TODO - switch to id later...
      const entity = this.entities.find((el) => el.id === this.inEntity).entity_name;
      const practice = this.practices.find((el) => el.id === this.inPractice).group_practice;

      httpService.get(`${Config.apiRoot}/reports/executive-summary/${encodeURIComponent(entity)}/${encodeURIComponent(practice)}/${this.inAuditDt}`, async (status, data) => {
        console.log("status - ", status);
        if (data.length == 0) {
          return;
        }
        this.reportData = data;
        await this.sleep(2000); // seems to need this delay for display of PDF

        var element = document.getElementById("toPrint");
        var opt = {
          margin: [0.25, 0.5, 0.25, 0.25],
          filename: "ExecutiveSummary.pdf",
          image: { type: "jpeg", quality: 1 },
          html2canvas: { scale: 2 },
          pagebreak: { mode: ["avoid-all", "css", "legacy"] },
          jsPDF: { unit: "in", format: "letter", orientation: "landscape" },
          jsPDFXX: { unit: "mm", format: "a4", orientation: "landscape" },
        };

        // New Promise-based usage:
        html2pdf()
          .set(opt)
          .from(element)
          .save();
      });
    },
    computeTotalRows(type) {
      return this.reportData
        .filter((el) => el.audit_type === type)
        .reduce((sum, count) => {
          return sum + count.row_count;
        }, 0);
    },
    computePracticeAuditLevel(type) {
      return this.computeRisk(this.computePracticeScore(type));
    },
    computePracticeScore(type) {
      // TODO - function in mixins (params deductions, totalrows)

      // rows = number of providers
      if (this.computeTotalRows == 0) {
        return "";
      } else {
        const filteredData = this.reportData.filter((el) => el.audit_type === type);

        const totalScore = filteredData.length * 100;
        let totalDeductions = 0;
        for (let i = 0; i < filteredData.length; i++) {
          totalDeductions += 100 - this.computeProviderScore(filteredData[i].audit_score, filteredData[i].row_count).toFixed(0);
        }

        return ((totalScore - totalDeductions) / filteredData.length).toFixed(0);
      }
    },
  },
};
</script>

