<template>
  <div>
    <v-container>
      <v-layout row wrap>
        <v-flex v-if="isAdministrator" xs12 md4 offset-md8 class="pr-1" right>
          <v-select v-model="auditorId" :items="auditors" item-text="fullname" item-value="id" box label="Auditors"></v-select>
        </v-flex>
        <v-flex v-if="!isAdministrator" xs12 md2 offset-md10 class="pr-1" right>
          <v-btn @click="getAudits">Refresh</v-btn>
        </v-flex>
      </v-layout>
      <v-layout row wrap style="margin-bottom: 25px;">
        <v-flex xs12 md6 class="pr-1">
          <!-- TODO - Progress Tracker -->
          <v-layout row wrap>
            <v-flex md3 class="pr-1 border"></v-flex>
            <v-flex md3 class="pr-1 border">Projected</v-flex>
            <v-flex md3 class="pr-1 border">Completed</v-flex>
            <v-flex md3 class="pr-1 border">Remaining</v-flex>
          </v-layout>
          <v-layout row wrap>
            <v-flex md3 class="pr-1 border">Providers</v-flex>
            <v-flex md3 class="pr-1 border">{{ totals.provider_projected }}</v-flex>
            <v-flex md3 class="pr-1 border">{{ totals.provider_completed }}</v-flex>
            <v-flex md3 class="pr-1 border">{{ totals.provider_remaining }}</v-flex>
          </v-layout>
          <v-layout row wrap>
            <v-flex md3 class="pr-1 border">Encounters</v-flex>
            <v-flex md3 class="pr-1 border">{{ totals.encounter_projected }}</v-flex>
            <v-flex md3 class="pr-1 border">{{ totals.encounter_completed }}</v-flex>
            <v-flex md3 class="pr-1 border">{{ totals.encounter_remaining }}</v-flex>
          </v-layout>
          <v-layout row wrap>
            <v-flex md3 class="pr-1 border">Educations</v-flex>
            <v-flex md3 class="pr-1 border">{{ totals.educations_projected }}</v-flex>
            <v-flex md3 class="pr-1 border">{{ totals.educations_completed }}</v-flex>
            <v-flex md3 class="pr-1 border">{{ totals.educations_remaining }}</v-flex>
          </v-layout>
        </v-flex>
        <v-flex xs12 md6 class="pr-1">
          <!-- TODO - Notifications -->
          <v-layout row wrap>
            <v-flex md6 offset-md3 class="pr-1 border">
              <div @click="setFilterNewProvider" class="hover">New Provider Education [{{ newAssignmentsCount }}]</div>
            </v-flex>
          </v-layout>
          <v-layout row wrap>
            <v-flex md6 offset-md3 class="pr-1 border">
              <!-- <div
                @click="setFilterTerminated"
                class="hover"
              >Terminated Providers [{{ terminatedCount }}]</div>-->
              <div>Terminated Providers [{{ terminatedCount }}]</div>
            </v-flex>
          </v-layout>
          <v-layout row wrap>
            <v-flex md6 offset-md3 class="pr-1 border">
              <div @click="setFilterRisk" class="hover">High Risk [{{ highRiskScoreCount }}]</div>
            </v-flex>
          </v-layout>
          <v-layout row wrap>
            <v-flex md6 offset-md3 class="pr-1 border">
              <div @click="setFilterAll" class="hover">View All Audits</div>
            </v-flex>
          </v-layout>
          <!-- <v-layout row wrap>
            <v-flex md6 offset-md3 class="pr-1 border">Require Re-Audit []</v-flex>
          </v-layout>-->
        </v-flex>
      </v-layout>

      <v-layout row wrap>
        <v-flex xs12 md12 class="pr-1">
          <v-card class="mb-2">
            <v-card-title class="headline font-weight-regular chsli-color white--text">Audit Assignments {{ this.auditFilterHeader }}</v-card-title>
            <v-card-text>
              <v-text-field v-model="searchDataTable" append-icon="search" label="Search" single-line hide-details></v-text-field>
              <v-data-table dense :key="key" :headers="tableHeaders" :items="filteredAudits" class="evelation-1" :pagination.sync="pagination" :search="searchDataTable">
                <template slot="items" slot-scope="{ item }">
                  <tr @clickaa="auditPhysician(item.npi, item.provider_audit_id)">
                    <td>{{ item.status }}</td>
                    <td>{{ item.daysInQueue }}</td>
                    <td>{{ item.provider_name }}</td>
                    <td>{{ item.entity_name }} - {{ item.group_practice }}</td>
                    <td>{{ item.audit_date }}</td>
                    <td>{{ item.med_system }} / {{ item.billing_system }}</td>
                    <td>{{ item.audit_type }}</td>
                    <td>{{ item.in_progress + item.complete }}</td>
                    <td>{{ item.in_progress }}</td>
                    <td>{{ item.complete }}</td>
                    <td>{{ item.current_audit_score }}</td>
                    <td style="display:none;">{{ item.encounter_list }}</td>
                    <td>
                      <v-tooltip bottom style="float:left;">
                        <i class="material-icons" @click="auditPhysician(item.npi, item.provider_audit_id)" slot="activator">edit</i>
                        <span>View/Edit Encounter</span>
                      </v-tooltip>
                      <v-tooltip bottom v-if="isAdministrator">
                        <i class="material-icons" @click="deleteAudit(item.provider_audit_id, $event)" slot="activator">delete</i>
                        <span>Delete Audit and All Associated Encounters</span>
                      </v-tooltip>
                    </td>
                  </tr>
                </template>
              </v-data-table>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
    <confirmation-dialog ref="confirmationDialog" :title="confirmation.title" :message="confirmation.message"></confirmation-dialog>
  </div>
</template>

<script>
import Config from "../config";
import Router from "../router";
import httpService from "../httpService";
import ConfirmationDialog from "@/components/ConfirmationDialog";
import CustomSnackbar from "@/components/Snackbar";

export default {
  name: "Main",
  components: { ConfirmationDialog, CustomSnackbar },
  data: () => ({
    audits: [],
    auditFilter: "",
    auditFilterHeader: "",
    terminated: [],
    auditorId: null,
    searchDataTable: null,
    key: 0,
    totals: {
      provider_projected: 0,
      provider_completed: 0,
      provider_remaining: 0,
      encounter_projected: 0,
      encounter_completed: 0,
      encounter_remaining: 0,
      educations_projected: 0,
      educations_completed: 0,
      educations_remaining: 0,
    },
    tableHeaders: [
      { text: "Status", value: "status" },
      { text: "Days in Queue", value: "daysInQueue" },
      { text: "Provider", value: "provider_name" },
      { text: "Practice", value: "practice" },
      { text: "Audit Date", value: "audit_date" },
      { text: "Med/Billing System", value: "med_system" },
      { text: "Audit Type", value: "audit_type" },
      { text: "Total Encounters", value: "total_encounters" },
      { text: "In Progress", value: "in_progress" },
      { text: "Completed", value: "complete" },
      { text: "Score", value: "current_audit_score" },
      { text: "Actions", value: "adminDelete" },
      { text: "Encounters", value: "encounter_list", align: " d-none" },
    ],
    pagination: {
      rowsPerPage: 10,
    },
    confirmation: {
      title: "",
      message: "",
    },
  }),
  async mounted() {
    this.auditorId = this.currentUser.id; // when are computed items ready?
    this.getAudits();
  },
  watch: {
    auditorId: function(id) {
      this.getAudits();
    },
  },
  computed: {
    auditors() {
      return this.$store.getters.ALL_USERS;
    },
    currentUser() {
      return this.$store.getters.CURRENT_USER;
    },
    physicians() {
      return this.$store.getters.PHYSICIAN;
    },
    physicianLookup() {
      return this.physicians.map((row) => {
        let res = {};
        res.name = row.last_name + ", " + row.first_name;
        res.npi = row.npi;
        return res;
      });
    },
    highRiskScoreCount() {
      let kntr = 0;
      this.audits.map((row) => {
        if (this.computeRisk(row.current_audit_score) === "High") {
          kntr++;
        }
      });
      return kntr;
    },
    newAssignmentsCount() {
      let kntr = 0;
      this.audits.map((row) => {
        if (row.education_completed_count == 0) {
          kntr++;
        }
      });
      return kntr;
    },
    terminatedCount() {
      let kntr = 0;
      this.audits.map((row) => {
        if (row.provider_terminated == "") {
          kntr++;
        }
      });
      return kntr;
    },
    isAdministrator() {
      return this.currentUser.role === "administrator";
    },
    filteredAudits() {
      // for links to filter records
      // "NEW/TERM/RISK"
      switch (this.auditFilter) {
        case "NEW":
          return this.audits.filter((aud) => aud.education_completed_count === 0);
          break;
        case "TERM":
          return this.audits.filter((aud) => aud.provider_terminated == "");
          break;
        case "RISK":
          return this.audits.filter((aud) => this.computeRisk(aud.current_audit_score) === "High");
          break;
        default:
          return this.audits;
      }
    },
  },
  methods: {
    setFilterNewProvider() {
      this.auditFilter = "NEW";
      this.auditFilterHeader = " - New Provider Education";
    },
    setFilterTerminated() {
      this.auditFilter = "TERM";
      this.auditFilterHeader = " - Terminated Providers";
    },
    setFilterRisk() {
      this.auditFilter = "RISK";
      this.auditFilterHeader = " - High Risk";
    },
    setFilterAll() {
      this.auditFilter = "ALL";
      this.auditFilterHeader = "";
    },
    getAudits() {
      if (!this.auditorId) {
        this.auditorId = this.currentUser.id;
      }

      httpService.get(`${Config.apiRoot}/audit/dashboard2/${this.auditorId}`, (status, data) => {
        this.audits = data.message.filter(
          // filter out the practice audits w/o provider's assigned yet
          (obj) => obj.provider_audit_id !== null
        );
        this.terminated = data.message2; // TODO - is this needed, is this all providers or just providers with an audit active

        //preset 2 fields for sorting
        for (let i = 0; i < this.audits.length; i++) {
          this.audits[i].status = this.getStatus(this.audits[i]);
          this.audits[i].in_progress = this.calcIncomplete(this.audits[i]);

          // days diff
          this.audits[i].daysInQueue = this.computeQueueTime(this.audits[i].audit_type, this.audits[i].epic_creation_date, this.audits[i].audit_completed_date);
        }
        this.computeTotals();
        this.key++;
      });
    },
    computeTotals() {
      this.totals.provider_projected = 0;
      this.totals.provider_completed = 0;
      this.totals.provider_remaining = 0;
      this.totals.encounter_projected = 0;
      this.totals.encounter_completed = 0;
      this.totals.encounter_remaining = 0;
      this.totals.educations_projected = 0;
      this.totals.educations_completed = 0;
      this.totals.educations_remaining = 0;

      for (let i = 0; i < this.audits.length; i++) {
        this.totals.provider_projected++; // one for each provider in list
        if (this.audits[i].status == "Completed") {
          this.totals.provider_completed++;
        }
        this.totals.provider_remaining = this.totals.provider_projected - this.totals.provider_completed;
        this.totals.encounter_projected += this.audits[i].in_progress + this.audits[i].complete;
        this.totals.encounter_completed += this.audits[i].complete;
        this.totals.encounter_remaining += this.audits[i].in_progress;
        this.totals.educations_projected++; // one for each provider in list
        if (this.audits[i].education_completed_count > 0) {
          this.totals.educations_completed += 1;
        }
        this.totals.educations_remaining = this.totals.educations_projected - this.totals.educations_completed;
      }
      this.totals;
    },
    deleteAudit(auditId, event) {
      console.log("auditId", auditId);

      event.stopPropagation();

      this.confirmation.title = "Delete Audit?";
      this.confirmation.message = "Are you sure you want to delete this audit?";
      this.$refs.confirmationDialog.confirmation().then((res) => {
        if (res) {
          httpService.delete(`${Config.apiRoot}/audit/provider/${auditId}`, (status, data) => {
            if (data.success) {
              //                this.showMessage("success", "Audit sucessfully deleted");
              this.popAudit(auditId);
            } else {
              this.showMessage("error", "There was a problem deleting the audit.  MSG - " + data.message.originalError.info.message);
            }
          });
        }
      });
    },
    calcIncomplete(provider) {
      // incomplete returns 1 if there are no joined rows, check then adjust
      if (provider.count_of_linked_rows === 0) {
        return 0;
      }
      return provider.incomplete;
    },
    getStatus(provider) {
      if (provider.audit_completed_date) {
        return "Completed";
      }
      return "In Progress";
    },
    auditPhysician(npi, provider_audit_id) {
      // user selected a record to work on
      Router.push({
        name: "audit-provider",
        params: { npi: npi, provider_audit_id: provider_audit_id },
      });
    },
    popAudit: function(id) {
      // remove item from the display list based on audit id
      this.audits.splice(this.audits.findIndex((item) => item.provider_audit_id == id), 1);
    },
    showMessage: function(type, message) {
      this.$refs.customSnackbar.showSnackbar(message, type, 3000, true);
    },
  },
};
</script>

<style>
.v-datatable th,
.v-datatable td {
  padding-left: 5px !important;
  padding-right: 5px !important;
}
/* .border {
  border: solid;
} */
i {
  padding: 0 5px;
}
i:hover {
  cursor: pointer;
}
.hover {
  cursor: pointer;
  text-decoration: underline;
}
.small-font {
  font-size: 15px;
}
.chsli-color {
  background-color: #320484 !important;
}
</style>
