<template>
  <div>
    <v-layout>
      <v-flex>
        <h3>Audit Scorecard</h3>
      </v-flex>
    </v-layout>
    <v-layout>
      <v-flex mr-1>
        <v-autocomplete v-model="inNPI" :items="physicians" persistent-hint item-text="name" item-value="npi" @click="reset()"></v-autocomplete>
      </v-flex>
      <v-flex>
        <v-text-field v-model="inAuditDt" label="Audit Date (MMYYYY)"></v-text-field>
      </v-flex>
      <v-flex>
        <v-btn @click="generateReport()">Run</v-btn>
      </v-flex>
    </v-layout>

    <div id="toPrint" style="width:95%">
      <v-container fluid>
        <v-layout>
          <v-flex xs2 cols="2">
            <img width="125" src="../assets/logo.png" />
          </v-flex>
          <v-flex>
            <div
              style="
                font-size: 2em;
                font-weight: bold;
                margin-left: 250px;
                padding-top: 15px;
              "
            >
              {{ reportTitle }}
            </div>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
        <div class="provider">
          <v-layout>
            <v-flex xs6 cols="6">
              <div>
                <span class="heading">Provider Name:</span>
                {{ reportData[0].last_name }}, {{ reportData[0].first_name }}
                {{ reportData[0].title }}
              </div>
            </v-flex>
            <v-flex xs3 cols="3">
              <div>
                <span class="heading">Score:</span>
                {{ providerAuditScore }}
              </div>
            </v-flex>
            <v-flex>
              <div>
                <span class="heading">Risk Level:</span>
                {{ providerAuditLevel }}
              </div>
            </v-flex>
          </v-layout>
          <v-layout>
            <v-flex xs9 cols="9">
              <div>
                <span class="heading">Entity:</span>
                {{ reportData[0].entity_name }}
              </div>
            </v-flex>
            <v-flex>
              <div>
                <span class="heading">Audit Date:</span>
                {{ reportData[0].audit_date }}
              </div>
            </v-flex>
          </v-layout>
          <v-layout>
            <v-flex xs9 cols="9">
              <div>
                <span class="heading">Practice Name:</span>
                {{ reportData[0].group_practice }}
              </div>
            </v-flex>
            <v-flex>
              <div>
                <span class="heading">NPI:</span>
                {{ reportData[0].npi }}
              </div>
            </v-flex>
          </v-layout>
        </div>
        <div v-for="(row, indx) in reportData" :key="indx">
          <v-layout mb-2>
            <v-flex xs4> <span class="heading">Patient:&nbsp;</span>{{ row.patient_name }} </v-flex>
            <v-flex xs4> <span class="heading">DOB:&nbsp;</span>{{ row.date_of_birth }} </v-flex>
            <v-flex xs4> <span class="heading">DOS:&nbsp;</span>{{ row.service_date }} </v-flex>
            <v-flex xs4> <span class="heading">Score:&nbsp;</span>{{ 10 - row.audit_score }} </v-flex>
          </v-layout>
          <v-layout dense class="center-title" mb-2>
            <v-flex>
              <span class="heading">CPT Code Billed</span>
              <div>{{ row.cpt_billed }}</div></v-flex
            >
            <v-flex>
              <span class="heading">CPT Code Supported</span>
              <div>{{ row.cpt_audited }}</div>
            </v-flex>
            <v-flex>
              <span class="heading">ICD-10 Billed</span>
              <div v-html="formatMultiRow(row.icd10_1_billed)"></div>
            </v-flex>
            <v-flex>
              <span class="heading">ICD-10 Supported</span>
              <div v-html="formatMultiRow(row.icd10_1_audited)"></div>
            </v-flex>
            <v-flex>
              <span class="heading">Modifier Billed</span>
              <div>{{ row.modifier_billed }}</div>
            </v-flex>
            <v-flex>
              <span class="heading">Modifier Supported</span>
              <div>{{ row.modifier_audited }}</div>
            </v-flex>
            <v-flex>
              <span class="heading">Units Billed</span>
              <div>{{ row.units_billed }}</div>
            </v-flex>
            <v-flex>
              <span class="heading">Units Supported</span>
              <div>{{ row.units_audited }}</div>
            </v-flex>
          </v-layout>
          <v-layout mb-2>
            <v-flex>
              <span class="heading">Comments:</span>
              <div v-html="formatJSONMultiRow(row.comments).replace(/cpt_audited_score/, row.cpt_billed)"></div>
            </v-flex>
          </v-layout>
          <v-layout>
            <v-flex>
              <span class="heading">Actions:</span>
              <div v-html="formatJSONMultiRow(row.fail_action).replace(/cpt_audited_score/, row.cpt_billed)"></div>
            </v-flex>
          </v-layout>

          <v-divider style="margin-top: 20px;margin-bottom: 20px"></v-divider>
        </div>
      </v-container>
    </div>
  </div>
</template>

<script>
import httpService from "../httpService";
import html2pdf from "html2pdf.js";

import Config from "../config";

// function addScript(url) {
//      var script = document.createElement('script');
//      script.type = 'application/javascript';
//      script.src = url;
//      document.head.appendChild(script);
//  }
//  addScript('https://raw.githack.com/eKoopmans/html2pdf/master/dist/html2pdf.bundle.js');

export default {
  data: () => ({
    reportTitle: "Audit Scorecard",
    reportData: [
      {
        last_name: "",
        first_name: "",
        title: "",
        score: 0,
        level: "",
        entity: "",
        audit_date: "",
        practice_name: "",
        patient_name: "",
        audit_score: "",
        npi: "",
      },
    ],
    inNPI: "",
    inAuditDt: "",
  }),
  computed: {
    physicians() {
      return this.$store.getters.ALL_PHYSICIAN.map((row) => {
        let res = {};
        res.name = row.last_name + ", " + row.first_name;
        res.npi = row.npi;
        return res;
      });
    },
    providerAuditScore() {
      let rows = 0;
      let score = 0;

      //     think i need to get distinct provider rows
      const distinctPatientRows = [...new Set(this.reportData.map((i) => i.audit_id))];

      for (let i = 0; i < distinctPatientRows.length; i++) {
        score += this.reportData.find((r) => r.audit_id === distinctPatientRows[i]).audit_score;
        rows++;
      }

      return this.computeProviderScore(score, rows);
    },
    providerAuditLevel() {
      return this.computeRisk(this.providerAuditScore);
    },
  },
  methods: {
    sleep(ms) {
      return new Promise((resolve) => setTimeout(resolve, ms));
    },
    formatMultiRow(str) {
      if (str) {
        // return JSON.stringify(JSON.parse(str), null, 2);
        // only find commas not between double quotes
        // return str.replace(/(?!\B"[^"]*),(?![^"]*"\B)/g, "<br>");
        return str.replace(/(,)(?=(?:[^"]|"[^"]*")*$)/g, "<br>");
      }
      return "";
    },
    formatJSONMultiRow(str) {
      if (str) {
        return str
          .replace(/(,)(?=(?:[^"]|"[^"]*")*$)/g, "<br>") // breaks on commas not in quotes
          .replace(/[{}"]+/g, "") // removes the json tags
          .replace(/units_audited_score/, "Units Audited")
          .replace(/units_supported_score/, "Units Supported")
          .replace(/modifier_audited_score/, "Modifier Audited")
          .replace(/modifier_supported_score/, "Modifier Supported")
          .replace(/null/g, "");
      }
      return "";
    },
    generateReport() {
      httpService.get(`${Config.apiRoot}/reports/audit-scorecard/${this.inNPI}/${this.inAuditDt}`, async (status, data) => {
        console.log("status - ", status);
        if (data.length == 0) {
          return;
        }
        this.reportData = data.sort(function(a, b) {
          var nameA = a.patient_name.toUpperCase(); // ignore upper and lowercase
          var nameB = b.patient_name.toUpperCase(); // ignore upper and lowercase
          if (nameA < nameB) {
            return -1;
          }
          if (nameA > nameB) {
            return 1;
          }

          // names must be equal
          return 0;
        });

        await this.sleep(4000); // seems to need this delay for display of PDF
        // this.$refs.html2Pdf.generatePdf();
        var element = document.getElementById("toPrint");
        var opt = {
          margin: [0.25, 0.5, 0.25, 0.25],
          filename: "Scorecard.pdf",
          image: { type: "jpeg", quality: 1 },
          html2canvas: { scale: 2 },
          pagebreak: { mode: "avoid-all" },
          jsPDF: { unit: "in", format: "letter", orientation: "landscape" },
        };

        // New Promise-based usage:
        html2pdf()
          .set(opt)
          .from(element)
          .save();
      });
    },
  },
};
</script>

