<template>
  <div>
    <v-container>
      <custom-snackbar ref="customSnackbar"></custom-snackbar>
      <v-card class="mb-2">
        <v-card-title class="headline font-weight-regular chsli-color white--text">Open Practice/Hospital Department Audits</v-card-title>
        <v-card-text>
          <v-list :key="auditComponentKey">
            <v-expansion-panel focusable>
              <v-expansion-panel-content v-for="practice in distinctPractices" :key="practice">
                <div slot="header">{{ practice }}</div>
                <v-expansion-panel>
                  <v-expansion-panel-content v-for="(auditDate, index) in distinctAuditDatesPerPractice(practice)" :key="index">
                    <div slot="header" class="indent-1">{{ auditDate }}</div>
                    <v-expansion-panel>
                      <v-expansion-panel-content key="-1" hide-actions>
                        <div slot="header" class="indent-3">
                          <v-layout row wrap>
                            <v-flex xs12 md6>
                              <v-btn @click="providerSelection(practice, auditDate)">Add Provider</v-btn>
                            </v-flex>
                          </v-layout>
                        </div>
                      </v-expansion-panel-content>
                      <v-expansion-panel-content hide-actions v-for="(item, index) in audits" :key="index" v-if="item.group_practice === practice && item.audit_date === auditDate">
                        <div v-if="item.provider_name" slot="header" class="indent-3" @click="getProviderAudits(item.npi, item.provider_audit_id, item.practice_id, item.practice_audit_id)">
                          <v-layout row wrap>
                            <v-flex xs12 md6>Provider: {{ item.provider_name }}</v-flex>
                            <v-flex xs12 md6>Audit Count: ???</v-flex>
                          </v-layout>
                        </div>
                        <div v-else slot="header" class="indent-3">
                          <v-layout row wrap>
                            <v-flex xs12 md6>No Providers</v-flex>
                          </v-layout>
                        </div>
                      </v-expansion-panel-content>
                    </v-expansion-panel>
                  </v-expansion-panel-content>
                </v-expansion-panel>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-list>
        </v-card-text>
      </v-card>

      <v-card class="mb-2" v-if="selectedPractice && selectedPracticeAuditId && !selectedPhysicianAuditId">
        <v-card-title class="headline font-weight-regular chsli-color white--text">Provider Selection</v-card-title>
        <v-card-text>
          <v-layout row wrap>
            <v-flex xs12 md6 pr-2>
              <v-autocomplete v-model="selectedNpi" :items="physicianLookup" persistent-hint item-text="name" item-value="npi" label="Provider"></v-autocomplete>
            </v-flex>
            <v-flex xs12 md4 offset-md1>
              <v-btn block color="blue" @click="startPhysicianAudit()">Start New Audit for Provider</v-btn>
            </v-flex>
          </v-layout>
        </v-card-text>
      </v-card>

      <patient-selection ref="PatientSelection"></patient-selection>
    </v-container>
  </div>
</template>

<script>
import Config from "../config";
import CustomSnackbar from "@/components/Snackbar";
import PatientSelection from "@/components/PatientSelection";
import httpService from "../httpService";

export default {
  name: "Main",
  components: { CustomSnackbar, PatientSelection },
  data: () => ({
    auditComponentKey: 0,
    audits: [],
    selectedPractice: null,
    selectedPracticeAuditId: null,
    selectedPhysicianAuditId: null,
    physicianAudits: [],
    practiceAudits: [],
    payerListing: [],
    procedureListing: [],
    selectedEncounters: [],
    menu1: false,
    menu2: false,
    initialSearch: false,
    auditMonth: null,
    auditYear: null,
    auditBeginDate: "2019-01-01",
    auditEndDate: "2019-07-01",
    auditPayer: null,
    selectedNpi: "",
    months: ["01", "02", "03", "04", "05", "06", "07", "08", "09", "10", "11", "12"],
  }),
  mounted() {
    httpService.get(`${Config.apiRoot}/audit/dashboard/${this.currentUser.id}`, (status, data) => {
      this.audits = data.message;
    });
    httpService.get(`${Config.apiRoot}/transactions/payers`, (status, data) => {
      // reorder so medicare and medicaid are first
      const reorderItems = ["MEDICARE", "MEDICAID"];
      reorderItems.forEach((el) => {
        let indx = data
          .map(function(e) {
            return e.original_payor;
          })
          .indexOf(el);

        if (indx > -1) {
          data.splice(indx, 1);
        }
        data.unshift(el);
      });

      this.payerListing = [...data];
    });
  },
  watch: {
    // selectedPractice: function(val) {
    //   this.getAudits(val);
    //   this.selectedPracticeAuditId = null;
    // },
    // selectedNpi: function(val) {
    //   this.getPhysicianAudits(val);
    //   this.selectedPhysicianAuditId = null;
    // }
  },
  computed: {
    years() {
      return this.rangeYears();
    },
    currentUser() {
      return this.$store.getters.CURRENT_USER;
    },
    distinctPractices() {
      return [...new Set(this.audits.map((item) => item.group_practice))];
    },
    entities() {
      return this.$store.getters.ENTITY;
    },
    practices() {
      return this.$store.getters.PRACTICE;
    },
    physicians() {
      return this.$store.getters.PHYSICIAN.filter((p) => p.physician_practice_id === this.selectedPractice);
    },
    physicianLookup() {
      return this.physicians
        .filter((row) => row.billing != "N")
        .map((row) => {
          let res = {};
          res.name = row.last_name + ", " + row.first_name;
          res.npi = row.npi;
          return res;
        });
    },
    distinctProcedures() {
      return [...new Set(this.procedureListing.map((obj) => obj.cpt))];
    },
    filteredPractices() {
      return this.practices.filter((obj) => obj.entity_id === this.selectedEntityId);
    },
  },
  methods: {
    forceRerender() {
      this.auditComponentKey += 1;
    },
    showMessage: function(type, message) {
      this.$refs.customSnackbar.showSnackbar(message, type, 5000, true);
    },
    getProviderAudits: function(npi, provider_audit_id, practice, auditDate) {
      console.log("getting provider audits");
      this.selectedNpi = npi;
      this.selectedPhysicianAuditId = provider_audit_id;
      this.selectedPractice = practice;
      this.selectedPracticeAuditId = auditDate;

      this.initialSearch = false; // hide old searches
      this.$refs.PatientSelection.showComponent(this.selectedPhysicianAuditId, this.selectedNpi);
    },
    distinctAuditDatesPerPractice(practice) {
      // distinct audit dates for practices for the 2nd grouping of the list
      return [...new Set(this.audits.filter((obj) => obj.group_practice === practice).map((obj) => obj.audit_date))];
    },
    providerSelection: function(practice, auditDate) {
      // find practice and practice audit id
      const selAudit = this.audits.find((el) => el.group_practice === practice && el.audit_date === auditDate);

      this.selectedPractice = selAudit.practice_id;
      this.selectedPracticeAuditId = selAudit.practice_audit_id;
      this.selectedPhysicianAuditId = null;
    },
    startPracticeAudit: function() {
      console.log("Creating practice audit");
      httpService.post(
        `${Config.apiRoot}/audit/practice/audit`,
        {
          practice: this.selectedPractice,
          auditDate: this.auditMonth.concat(this.auditYear),
          allProviders: this.allProviders,
        },
        (status, data) => {
          if (!data.success) {
            this.showMessage("error", "There was a problem creating the Practice//Hospital Department audit records - " + data.message.originalError.info.message);
            return;
          }
          this.showMessage("success", "Practice/Hospital Department Audit records created.");
          //          this.selectedPractice = null;

          this.getAudits();
        }
      );
    },
    startPhysicianAudit: function() {
      httpService.post(
        `${Config.apiRoot}/audit/physician/audit`,
        {
          physician: this.selectedNpi,
          audit_practice_id: this.selectedPracticeAuditId,
        },
        (status, data) => {
          console.log("data - ", data);
          if (!data.success) {
            this.showMessage("error", "There was a problem creating the Practice/Hospital Department audit records - " + data.message.originalError.info.message);
            return;
          }
          this.selectedPhysicianAuditId = data.message.recordset[0].new_id;

          this.getAudits(); // refresh list
          this.showMessage("success", "Physician Audit record created.");
        }
      );
    },
    getAudits: function(val = this.selectedPractice) {
      console.log("getting audit");
      httpService.get(`${Config.apiRoot}/audit/practice/audit/${val}`, (status, data) => {
        if (!data.success) {
          this.showMessage("error", "There was a problem retrieving Practice/Hospital Department audit records");
          return;
        }
        this.practiceAudits = data.message;
        this.forceRerender();
      });
    },
    getPhysicianAudits: function(val = this.selectedNpi) {
      console.log("getting audit");
      httpService.get(`${Config.apiRoot}/audit/physician/audit/${val}`, (status, data) => {
        if (!data.success) {
          this.showMessage("error", "There was a problem retrieving Practice/Hospital Department audit records");
          return;
        }
        this.physicianAudits = data.message;
      });
    },
    async searchProcedures() {
      this.procedureListing = [];
      this.selectedEncounters = [];

      const qs = `${this.selectedNpi}/${this.auditBeginDate}/${this.auditEndDate}/${this.auditPayer}`;
      httpService.get(`${Config.apiRoot}/transactions/${qs}`, (status, data) => {
        if (data) {
          this.procedureListing = data;
        }
        this.initialSearch = true; // show no data only after the first search is done
      });
    },
    patientCptList(pt) {
      // pt - patient encounter
      // return array of CPT codes for a specific patient
      return [...new Set(this.patientRecords(pt).map((obj) => obj.cpt))];
    },
    patientRecords(pt) {
      // pt - patient encounter
      // return array of rows for a specific patient
      return this.procedureListing.filter((obj) => obj.encounter === pt);
    },
    listGroupCountForProcedures(proc) {
      return this.procedureListing.filter((obj) => obj.cpt === proc).length;
    },
    addPatientToList(enc) {
      // Add encounter to list to create audits
      if (!this.selectedEncounters.includes(enc)) {
        this.selectedEncounters.push(enc);

        // hide entry on the selection list
        //        const indx = this.procedureListing.map(el => el.encounter).indexOf(enc);
        //        this.procedureListing[indx].visible = false;
        this.procedureListing.map((el) => {
          if (el.encounter === enc) {
            el.visible = false;
          }
        });

        //        this.forceRerender(); // force redraw of the lists to hide same encounter there
      }
    },
    popPatientFromList(enc) {
      // Remove encounter from the list of audits
      this.selectedEncounters.splice(this.selectedEncounters.indexOf(enc), 1);

      // show entry back on the selection list
      const indx = this.procedureListing.map((el) => el.encounter).indexOf(enc);
      this.procedureListing[indx].visible = true;
    },
    async createAudits() {
      // create audit records in database
      console.log("creating audit records");
      // WADE- not used? const qs = `?enc=${this.selectedEncounters}`;
      //      return new Promise((resolve, reject) => {
      httpService.post(
        `${Config.apiRoot}/transactions/createAudits`,
        {
          physicianAuditId: this.selectedPhysicianAuditId,
          encounters: this.selectedEncounters,
        },
        (status, data) => {
          if (!data.status) {
            this.showMessage("error", "there was a problem creating the audit records");
          }
          this.showMessage("success", "Audit records created.  TODO - create detailed message");
          this.selectedEncounters = [];
        }
      );
      //     });
    },
    patientFromEncounter(enc) {
      return this.procedureListing.find((elem) => elem.encounter === enc);
    },
  },
};
</script>

<style>
i:hover {
  cursor: pointer;
}

.indent-1 {
  margin-left: 15px;
}

.indent-2 {
  margin-left: 30px;
}

.indent-3 {
  margin-left: 45px;
}

.record-selector .v-list__tile {
  height: auto;
}
</style>
