<template>
  <div>
    <v-layout>
      <v-flex>
        <h3>Audit History</h3>
      </v-flex>
    </v-layout>
    <v-layout>
      <v-flex>
        <v-autocomplete v-model="inNPI" :items="physicians" persistent-hint item-text="name" item-value="npi" @click="reset()"></v-autocomplete>
      </v-flex>
      <v-flex>
        <v-btn @click="generateReport()">Run</v-btn>
      </v-flex>
    </v-layout>

    <div v-if="!initialSearch">
      <v-layout>
        <v-flex xs2 cols="2">
          <img width="125" src="../assets/logo.png" />
        </v-flex>
        <v-flex>
          <div style="font-size:2em;font-weight:bold;margin:auto;text-align:center;padding-top:10px;">{{ reportTitle }} - {{ reportData[0].provider_name }} ({{ reportData[0].npi }})</div>
        </v-flex>
      </v-layout>
      <v-layout dense>
        <v-flex xs2>
          <span class="heading">Entity</span>
        </v-flex>
        <v-flex xs2 cols="2">
          <span class="heading">Practice/Hospital Department</span>
        </v-flex>
        <v-flex xs2>
          <span class="heading">Group Name</span>
        </v-flex>
        <v-flex xs2>
          <span class="heading">Auditor</span>
        </v-flex>
        <v-flex xs2>
          <span class="heading">CMO</span>
        </v-flex>
        <v-flex xs2>
          <span class="heading">Director</span>
        </v-flex>
        <v-flex xs2>
          <span class="heading">Contact Person</span>
        </v-flex>
        <v-flex xs2>
          <span class="heading">Audit Type</span>
        </v-flex>
        <v-flex xs2>
          <span class="heading">Audit Score</span>
        </v-flex>
        <v-flex xs2>
          <span class="heading">Risk Level</span>
        </v-flex>
        <v-flex xs2>
          <span class="heading">Completed Date</span>
        </v-flex>
      </v-layout>
      <div v-for="(row, indx) in reportData" :key="indx">
        <v-layout dense style="font-size:12px;">
          <v-flex xs2>{{ row.entity_name }}</v-flex>
          <v-flex xs2 cols="2">{{ row.group_practice }}</v-flex>
          <v-flex xs2>{{ row.group_name }}</v-flex>
          <v-flex xs2>{{ row.auditor }}</v-flex>
          <v-flex xs2>{{ row.chief_medical_officer }}</v-flex>
          <v-flex xs2>{{ row.practice_director }}</v-flex>
          <v-flex xs2>{{ row.contact1_name }}</v-flex>
          <v-flex xs2>{{ row.audit_type }}</v-flex>
          <v-flex xs2>{{ row.audit_score }}</v-flex>
          <v-flex xs2>{{ row.audit_level }}</v-flex>
          <v-flex xs2>{{ row.audit_completed_date }}</v-flex>
        </v-layout>
      </div>
      <v-layout>
        <v-flex>
          <v-btn @click="csvExport()">Download File</v-btn>
        </v-flex>
      </v-layout>
    </div>
  </div>
</template>

<script>
import httpService from "../httpService";
const { parse } = require("json2csv");

import Config from "../config";
export default {
  data: () => ({
    initialSearch: true,
    reportTitle: "Audit History",
    reportData: [
      {
        entity_name: "",
        group_practice: "",
        group_name: "",
        provider_name: "",
        npi: "",
        auditor: "",
        chief_medical_officer: "",
        practice_director: "",
        contact1_name: "",
        audit_completed_date: "",
        sum_audit_score: "",
        audit_rows: "",
        audit_type: "",
        audit_score: 0,
        audit_level: "",
      },
    ],
    inNPI: "1790762623",
  }),
  computed: {
    physicians() {
      return this.$store.getters.ALL_PHYSICIAN.map((row) => {
        let res = {};
        res.name = row.last_name + ", " + row.first_name;
        res.npi = row.npi;
        return res;
      });
    },
  },
  methods: {
    score(score, rows) {
      return Math.round(((10 * rows - score) / (10 * rows)) * 100);
    },
    level(score) {
      if (score === -1 || score === "" || score === null) {
        return "";
      } else if (score < 75) {
        return "High";
      } else if (score <= 84) {
        return "Moderate";
      } else {
        return "Low";
      }
    },
    sleep(ms) {
      return new Promise((resolve) => setTimeout(resolve, ms));
    },
    generateReport() {
      httpService.get(`${Config.apiRoot}/reports/audit-history/${this.inNPI}`, async (status, data) => {
        this.reportData = data;

        for (let i = 0; i < this.reportData.length; i++) {
          this.reportData[i].audit_score = this.score(this.reportData[i].sum_audit_score, this.reportData[i].audit_rows);
          this.reportData[i].audit_level = this.level(this.reportData[i].audit_score);
        }
        this.initialSearch = false;

        await this.sleep(4000); // seems to need this delay for display of PDF
      });
    },
    csvExport() {
      const fields = [
        "entity_name",
        "group_practice",
        "group_name",
        "auditor",
        "chief_medical_officer",
        "practice_director",
        "contact1_name",
        "audit_type",
        "audit_score",
        "audit_level",
        "audit_completed_date",
      ];
      const opts = { fields };

      try {
        const csvHeader = `Audit History - ${this.reportData[0].provider_name} (${this.reportData[0].npi})\n\n`;
        const csvData = parse(this.reportData, opts);

        let csvContent = "data:text/csv;charset=utf-8," + csvHeader + csvData;

        const data = encodeURI(csvContent);

        this.sampledata = csvContent;
        const link = document.createElement("a");
        link.setAttribute("href", data);
        link.setAttribute("download", "export.csv");
        link.click();
      } catch (err) {
        console.error(err);
      }
    },
  },
};
</script>

