<template>
  <div>
    <v-container>
      <custom-snackbar ref="customSnackbar"></custom-snackbar>
      <v-card class="mb-2">
        <v-card-title class="headline font-weight-regular chsli-color white--text">Create Audit for Practice/Hospital Department 2</v-card-title>
        <v-card-text>
          <v-layout row wrap>
            <v-flex xs12 md2 pr-1>
              <v-select v-model="selectedEntityId" :items="entities" item-text="entity_name" item-value="id" label="Entity"></v-select>
            </v-flex>
            <v-flex xs12 md6 pr-2>
              <v-autocomplete v-model="selectedPractice" :items="filteredPractices" persistent-hint item-text="group_practice" item-value="id" label="Practice/Hospital Department"></v-autocomplete>
            </v-flex>
            <v-flex>
              <v-select v-model="selectedPracticeAuditId" :items="practiceAudits" label="Open Audits" item-value="id" item-text="audit_date"></v-select>
            </v-flex>
          </v-layout>
          <v-layout row wrap v-if="selectedPracticeAuditId === -1">
            <v-flex xs12 md8 pr-1>
              <v-checkbox v-model="allProviders" label="Create Audit for All Providers?"></v-checkbox>
            </v-flex>
            <v-flex xs12 md2 pr-1>
              <v-select v-model="auditMonth" :items="months" label="Audit Month" name="Audit Month" @input="validate" :error-messages="errors.collect('Audit Month')"></v-select>
            </v-flex>
            <v-flex xs12 md2>
              <v-select v-model="auditYear" :items="years" label="Audit Year" name="Audit Year" @input="validate" :error-messages="errors.collect('Audit Year')"></v-select>
            </v-flex>
          </v-layout>
          <v-layout row wrap>
            <v-flex xs12 md4 offset-md4>
              <v-btn block color="blue" :disabled="selectedPracticeAuditId != -1" @click="startPracticeAudit()">Start New Audit</v-btn>
            </v-flex>
          </v-layout>
        </v-card-text>
      </v-card>

      <v-card class="mb-2" v-if="selectedPractice && selectedPracticeAuditId > 0">
        <!--  v-if="selectedPractice && selectedPracticeAuditId" OR v-if="false" -->
        <v-card-title class="headline font-weight-regular chsli-color white--text">Provider Selection</v-card-title>
        <v-card-text>
          <v-layout row wrap>
            <v-flex xs12 md6 pr-2>
              <v-autocomplete
                v-model="selectedNpi"
                :items="providerSelection"
                persistent-hint
                item-text="name"
                item-value="npi"
                label="Provider"
                hint="* - denotes provider has an existing audit created."
              ></v-autocomplete>
            </v-flex>
            <v-flex xs12 md6>
              <v-text-field v-model="selectedPhysicianLastEdDate" label="Last Education Date" disabled></v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row wrap>
            <v-flex xs12 md4 offset-md4>
              <v-btn block color="blue" :disabled="selectedNpiHasAudit === true" @click="startPhysicianAudit()">Start New Audit for Provider</v-btn>
            </v-flex>
          </v-layout>
        </v-card-text>
      </v-card>
      <patient-selection id="patientSelectionBlock" ref="PatientSelection" style="margin-top:10px;" v-on:created="init()"></patient-selection>
    </v-container>
  </div>
</template>

<script>
import CustomSnackbar from "@/components/Snackbar";
import Config from "../config";
import httpService from "../httpService";
import PatientSelection from "@/components/PatientSelection";

// auditBeginDate: "2017-12-01",
// auditEndDate: "2018-11-15",
// auditPayer: "MEDICARE",
// selectedNpi: "1245218619",
// auditBeginDate: null,
// auditEndDate: null,
// auditPayer: null,
// selectedNpi: null,
//    physicianAudits: [],

export default {
  name: "Main",
  components: { CustomSnackbar, PatientSelection },
  data: () => ({
    allProviders: false,
    practiceAudits: [],
    physicianAudits: [],
    physicianEducation: [],
    providerSelection: [],
    selectedEntityId: null,
    selectedPractice: null,
    selectedPracticeAuditId: null,
    selectedPhysicianAuditId: null,
    selectedNpi: null,
    selectedPhysicianLastEdDate: null,
    auditMonth: null,
    auditYear: null,
    months: ["01", "02", "03", "04", "05", "06", "07", "08", "09", "10", "11", "12"],
  }),
  mounted() {},
  computed: {
    years() {
      return this.rangeYears();
    },
    entities() {
      return this.$store.getters.ENTITY;
    },
    practices() {
      return this.$store.getters.PRACTICE;
    },
    physicians() {
      return this.$store.getters.PHYSICIAN.filter((p) => p.physician_practice_id === this.selectedPractice);
    },
    physicians_test() {
      return this.$store.getters.PHYSICIAN;
    },
    physicianLookup() {
      return this.physicians
        .filter((row) => row.billing != "N")
        .map((row) => {
          let res = {};
          res.name = row.last_name + ", " + row.first_name + " " + row.title;
          res.npi = row.npi;
          return res;
        });
    },
    filteredPractices() {
      return this.practices.filter((obj) => obj.entity_id === this.selectedEntityId);
    },
    selectedNpiHasAudit() {
      if (this.selectedNpi) {
        const provider = this.providerSelection.find((row) => {
          return row.npi === this.selectedNpi;
        });

        return provider.auditCreated;
      }
      return true; // default to disabled
    },
  },
  watch: {
    selectedPractice: function(val) {
      // fetch open audits when a practice is selected
      this.getPracticeAudits(val);
      this.selectedPracticeAuditId = null;
    },
    selectedPracticeAuditId: function(val) {
      this.getPhysicianAuditsforPractice(val);
    },
    selectedNpi: function() {
      if (this.selectedNpi) {
        const provider = this.providerSelection.find((row) => {
          return row.npi === this.selectedNpi;
        });
        this.selectedPhysicianAuditId = provider.auditId;

        // user selected a record to work on
        this.$refs.PatientSelection.showComponent(this.selectedPhysicianAuditId, this.selectedNpi);

        // retreive education for this provider and display
        // TODO selectedNpi;
        httpService.get(`${Config.apiRoot}/physician/education/latest/${this.selectedNpi}`, (status, data) => {
          if (!data) {
            this.showMessage("error", data);
            return;
          }
          this.selectedPhysicianLastEdDate = data[0].education_date; // physicians with existing audit record
        });
      }
    },
  },
  methods: {
    showMessage: function(type, message) {
      this.$refs.customSnackbar.showSnackbar(message, type, 5000, true);
    },
    validate: function() {
      // clear error messages after something was selected
      this.errors.clear();
    },
    getPhysicianAuditsforPractice: function(val) {
      console.log("getting physicians for practice audit");
      httpService.get(`${Config.apiRoot}/audit/practice/physician-audits/${val}`, (status, data) => {
        if (!data.success) {
          this.showMessage("error", "There was a problem retrieving physician audit records");
          return;
        }
        this.physicianAudits = data.message; // physicians with existing audit record
        this.providerSelection = this.physicianLookup; // set the list of providers in the practice

        for (let i = 0; i < this.providerSelection.length; i++) {
          // if the provider already has an audit, remove them from providerSelection
          const match = this.physicianAudits.filter((el) => {
            return el.npi === this.providerSelection[i].npi;
          });
          if (match.length > 0) {
            // this.providerSelection.splice(i, 1);
            // don't remove them?  just mark them?
            this.providerSelection[i].auditCreated = true;
            this.providerSelection[i].auditId = match[0].id; // physician audit record, used for patient selection
            this.providerSelection[i].name += " *";
          }
        }
      });
    },
    getPracticeAudits: function(val = this.selectedPractice) {
      // get open audits for a practice
      console.log("getting audits");
      httpService.get(`${Config.apiRoot}/audit/practice/audit/${val}`, (status, data) => {
        if (!data.success) {
          this.showMessage("error", "There was a problem retrieving Practice//Hospital Department audit records");
          return;
        }
        this.practiceAudits = data.message;

        // add "create audit" to the top of the list
        this.practiceAudits.unshift({ audit_date: "New", id: -1 });
      });
    },
    startPracticeAudit: function() {
      console.log("Creating practice audit");
      if (!this.auditMonth) {
        this.errors.add({
          field: "Audit Month",
          msg: "Audit Month is required when starting a new audit",
        });
        return;
      }
      if (!this.auditYear) {
        this.errors.add({
          field: "Audit Year",
          msg: "Audit Year is required when starting a new audit",
        });
        return;
      }

      httpService.post(
        `${Config.apiRoot}/audit/practice/audit`,
        {
          practice: this.selectedPractice,
          auditDate: this.auditMonth.concat(this.auditYear),
          allProviders: this.allProviders,
        },
        (status, data) => {
          if (!data.success) {
            this.showMessage("error", "There was a problem creating the practice/Hospital Department audit records - " + data.message.originalError.info.message);
            return;
          }
          this.showMessage("success", "Practice/Hospital Department Audit records created.");
          this.selectedNpi = null; // clear out the selected physician
          this.selectedPracticeAuditId = data.message.recordset[0].new_id;
        }
      );
    },
    startPhysicianAudit: function() {
      httpService.post(
        `${Config.apiRoot}/audit/physician/audit`,
        {
          physician: this.selectedNpi,
          audit_practice_id: this.selectedPracticeAuditId,
        },
        (status, data) => {
          if (!data.success) {
            this.showMessage("error", "There was a problem creating the Practice/Hospital Department audit records - " + data.message.originalError.info.message);
            return;
          }
          this.showMessage("success", "Physician Audit record created.");
          //          this.selectedPractice = null;
          this.selectedPhysicianAuditId = data.message.recordset[0].new_id;

          // user selected a record to work on
          this.$refs.PatientSelection.showComponent(this.selectedPhysicianAuditId, this.selectedNpi);

          // scroll to the new object
          document.getElementById("patientSelectionBlock").scrollIntoView(false);
        }
      );
    },
  },
};
</script>

<style>
i:hover {
  cursor: pointer;
}

.record-selector .v-list__tile {
  height: auto;
}
</style>
